<template>
  <div class="sidebar">
    <Logo v-if="showLogo" :collapse="isCollapse"></Logo>
    <el-menu router mode="vertical"
             class="menu"
             text-color="#999"
             style="text-align: left"
             :default-active="$route.path"
             :collapse="isCollapse"
             background-color="rgba(0,0,0,0)"
             unique-opened
             @select="currentIndexChange"
    >
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-s-data"></i>
          <span></span>
        </template>
        <el-menu-item index="/issuePublicity"></el-menu-item>
        <el-menu-item index="/lockedAnnouncement"></el-menu-item>
      </el-submenu>
      <el-submenu index="2" :route="'/'">
        <template slot="title">
          <i class="el-icon-s-custom"></i>
          <span></span>
        </template>
        <el-menu-item index="/seedExc"></el-menu-item>
        <el-menu-item index="/consensus"></el-menu-item>
        <el-menu-item index="/contribution"></el-menu-item>
        <el-menu-item index="/invite"></el-menu-item>
      </el-submenu>
      <el-submenu index="8">
        <template slot="title">
          <i class="el-icon-office-building"></i>
          <span></span>
        </template>
        <el-menu-item index="/RBTBank">RBT</el-menu-item>
        <el-menu-item index="/mortgageMarket"></el-menu-item>
        <el-menu-item index="/tokenTransfer"></el-menu-item>
      </el-submenu>

      <el-menu-item index="9"  :route="'/foundation'" :class="{'is-active':$route.path == '/foundation'}">
        <div class="nav-single-item">
          <i class="el-icon-menu"></i>
          <span></span>
        </div>
      </el-menu-item>
      <el-submenu index="3">
        <template slot="title">
          <i class="el-icon-box"></i>
          <span></span>
        </template>
        <el-menu-item index="/sourceOfIncome"></el-menu-item>
        <el-menu-item index="/incomeDistribution"></el-menu-item>
        <el-menu-item index="/incomeDividends"></el-menu-item>
      </el-submenu>
      <el-submenu index="4">
        <template slot="title">
          <i class="el-icon-medal"></i>
          <span>VIP</span>
        </template>
        <el-menu-item index="/myVip">VIP</el-menu-item>
        <el-menu-item index="/transferVip">VIP</el-menu-item>
      </el-submenu>
      <el-submenu index="5">
        <template slot="title">
          <i class="el-icon-chat-round"></i>
          <span></span>
        </template>
        <el-menu-item index="/communityToken"></el-menu-item>
        <el-menu-item index="/cityNodeTokenMarket"></el-menu-item>
        <el-menu-item index="/nodeList"></el-menu-item>
        <el-menu-item index="/globalGovernance"></el-menu-item>
<!--        <el-menu-item index="/forum"></el-menu-item>-->
      </el-submenu>

    </el-menu>
  </div>
</template>

<script>
import variables from '@/styles/variables.scss'
import Logo from './Logo'

export default {
  name: "sidebar",
  components: {Logo},
  data() {
    return {
      navArr: [[
        {
          path: '/issuePublicity',
          name: ''
        },
        {
          path: '/lockedAnnouncement',
          name: ''
        },
      ], [
        {
          path: '/seedSub',
          name: ''
        },
        {
          path: '/seedExc',
          name: ''
        },
        {
          path: '/consensus',
          name: ''
        },
        {
          path: '/contribution',
          name: ''
        },
        {
          path: '/invite',
          name: ''
        },
      ], [
        {
          path: '/RBTBank',
          name: 'RBT'
        },
        {
          path: '/mortgageMarket',
          name: ''
        },
        {
          path: '/tokenTransfer',
          name: ''
        },
      ], [
        {
          path: '/sourceOfIncome',
          name: ''
        },
        {
          path: '/incomeDistribution',
          name: ''
        },
        {
          path: '/incomeDividends',
          name: ''
        }
      ], [
        {
          path: '/myVip',
          name: 'VIP'
        },
        {
          path: '/transferVip',
          name: 'VIP'
        }
      ], [
        {
          path: '/communityToken',
          name: ''
        },
        {
          path: '/cityNodeTokenMarket',
          name: ''
        },
        {
          path: '/nodeList',
          name: ''
        },
        {
          path: '/globalGovernance',
          name: ''
        },
        {
          path: '/forum',
          name: ''
        }
      ],
      ]
    }
  },
  methods: {
    currentIndexChange(e) {
      let indexName = ["Rainbow DAO", "", "", "", "", "VIP", ""]
      this.navArr.forEach((nav, index) => {
        nav.forEach((item) => {
          if (item.path == e) {
            console.log(index, indexName[index + 1])
            this.$store.commit('app/SET_NAVNAME', indexName[index + 1])
            this.$store.commit('app/SET_NAVARR', nav)
          }
        })
      })
    }
  },
  computed: {
    isCollapse() {
      return !this.$store.state.app.sidebar.opened
    },
    activeMenu() {
      const route = this.$route
      const {meta, path} = route
      if (meta.activeMenu) {
        return meta.activeMenu
      }
      return path
    },
    showLogo() {
      return true
    },
    variables() {
      return variables
    },

  },
  created() {
    this.navArr.forEach(nav => {
      nav.forEach(item => {
        if (item.path == this.$route.path) {
          this.$store.commit('app/SET_NAVARR', nav)
        }
      })
    })
  }
}
</script>

<style lang="scss" scoped>
@import "~@/styles/variables.scss";

.sidebar {
  user-select: none;
}

.el-menu {
  border-right: solid 0px #e6e6e6;
}

.sidebar-container {
  background-size: 126% 110%;
  background: url("../../assets/img/silider_bg.png") 50% 40%;
  padding: 0!important;
  transition: width 0.28s;
  width: $sideBarWidth !important;
  height: 100%;
  position: fixed;
  font-size: 0px;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  overflow-x: hidden;

  // reset element-ui css
  .horizontal-collapse-transition {
    transition: 0s width ease-in-out, 0s padding-left ease-in-out, 0s padding-right ease-in-out;
  }

  .scrollbar-wrapper {
    overflow-x: hidden !important;
  }

  .el-scrollbar__bar.is-vertical {
    right: 0px;
  }

  .el-scrollbar {
    height: 100%;
  }

  &.has-logo {
    .el-scrollbar {
      height: calc(100% - 50px);
    }
  }

  .is-horizontal {
    display: none;
  }

  a {
    display: inline-block;
    width: 100%;
    overflow: hidden;
  }

  .svg-icon {
    margin-right: 16px;
  }

  .sub-el-icon {
    margin-right: 12px;
    margin-left: -2px;
  }

  .el-menu-item{
    height: 40px !important;
    &.is-active{
      .nav-single-item{
        color:$menuHoverColor!important;
        background-color: rgba(255,255,255,0.05)!important;
      }
    }
    .nav-single-item{
      padding-left: 16px !important;
      margin-left: -8px;
      border-top-left-radius: 20px;
      border-bottom-left-radius: 20px;
      width: 300px;
      height: 40px !important;
      line-height: 40px !important;
      margin-top: 10px;
      transition: 0.3s;
      &:hover{
        color: #A519BB !important;
        background-color: #f0effe !important;
        i{
          color: #A519BB !important;
        }
      }
    }
    &:hover{

      background: rgba(0,0,0,0) !important;
    }
  }
  & .nest-menu .el-submenu > .el-submenu__title,
  & .el-submenu .el-menu-item {
    min-width: $sideBarWidth !important;
  }
}

</style>
